@charset "utf-8";

/* CSS Document */


/*=====================通用样式定义========================*/

body {
    margin: 0;
    padding: 0;
    font-size: 14px;
    line-height: 0.24rem;
    font-family: "微软雅黑";
    position: relative;
}

form,
ul,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dd,
dl {
    margin: 0;
    padding: 0;
    font-weight: normal;
}

input,
select,
button {
    font-size: 12px;
    line-height: 16px;
    margin: 0;
    padding: 0;
    font-family: "微软雅黑";
}

table,
td {
    font-size: 12px;
    padding: 0;
    border-collapse: collapse;
}

img {
    border: 0;
    vertical-align: middle;
    display: block;
    width: 100%;
    height: auto;
}

.swiper-wrapper img {
    width: 100%;
}

.detail-title {
    margin-bottom: 0.3rem;
}

ul,
li {
    list-style-type: none;
}


/* 通用浮动属性 */

.fl {
    float: left;
}

.ft {
    float: left;
}

.fr {
    float: right;
}

.clear {
    clear: both;
}


/*文字对齐方式*/

.tc {
    text-align: center;
}

.tl {
    text-align: left;
}

.tr {
    text-align: right;
}


/*常用方法*/

.fontArial {
    font-family: 'Arial';
}

b {
    font-weight: normal;
}


/* 默认链接颜色 */

p {
    color: #333;
}

a {
    color: #333;
    text-decoration: none;
}

a:hover {
    color: #cc3341;
    text-decoration: none;
}

*:focus {
    outline: none;
}


